import {ProfileApi, usersApi} from "../Api/Api";

const ADD_POST='ADD-POST';
const UPDATE_NEW_POST_TEXT='UPDATE-NEW-POST-TEXT';
const SET_USERS_PROFILE='SET-USERS-PROFILE';
const SET_STATUS='GET-STATUS'


let intialState={
    Posts:[
        {Name:"Dima",post:"Hi",id:"1",like:'1'},
    ],
    Newtext: '',
    profile:{
        "userId": 15498,
        "photos": {
            "small": "'https://cdn.jpegmini.com/user/images/slider_puffin_before_mobile.jpg'",
            "large": "'https://cdn.jpegmini.com/user/images/slider_puffin_before_mobile.jpg'"
        }
    },
    status: 'Hello',
}

const Profile_reducer=(state=intialState,action)=>{

    switch (action.type) {


        case ADD_POST: {
            let newpost = {
                Name:'Gleb',
                post: action.text.post,
                id: '4',
                like: '1',
            }
            return{...state,
                Newtext:'',
                Posts:[...state.Posts,newpost]
            }
        }
        case UPDATE_NEW_POST_TEXT: {
            return {...state,Newtext:action.text}
        }
        case SET_USERS_PROFILE:{
            return{
                ...state,profile:action.profile
            }
        }
        case SET_STATUS:{
            return{
                ...state,status:action.status
            }
        }
        default:
            return state
    }
}

export default Profile_reducer;

export const  AddPostActionCreator=(text)=>{

    return{
            type:ADD_POST,
            text:text,
    }
}

export const UpdatePostActionCreator=(text)=>{
    return{
            type:UPDATE_NEW_POST_TEXT,
            text:text
    }
}

export const SetUsersProfile=(profile)=>{

    return{
        type:SET_USERS_PROFILE,
        profile
    }
}

export const SetStatus=(status)=>{
    return{
        type:SET_STATUS,
        status
    }
}

export const SetUsersProfileThunk = (userId) => async (dispatch) => {
    let response = await usersApi.setUsersProfileAxios(userId)
    dispatch(SetUsersProfile(response.data))


}

export const GetStatusThunk = (userId) => async (dispatch) => {
    let response = await ProfileApi.getStatusAxios(userId)
    dispatch(SetStatus(response.data))


}

export const UpdateThunk = (status) => async (dispatch) => {
    let response = await ProfileApi.updateStatusAxios(status)

    if (response.data.resultCode === 0) {
        dispatch(SetStatus(status))
    }

}






