import style from './Profileinfo.module.css'
import StatusHook from "../Status/StatusHook";
import userPhoto from "../../../../assets/images/default-avatar-profile-icon-male.jpg";
import React from "react";

const Profileinfo = (props) => {


    return (
        <div className={style.Profile}>
            <div>
                <img src={props.profile.photos.small != null ?props.profile.photos.small : userPhoto}/>
                <StatusHook status={props.status} UpdateThunk={props.UpdateThunk}/>


            </div>
        </div>
    )
}
export default Profileinfo;