import {LoginApi, usersApi} from "../Api/Api";


const SET_USER_DATA = 'SET-USER-DATA'
const LOGIN_ERROR = 'LOGIN-ERROR'


let intialState = {
    userId: null,
    email: null,
    login: null,
    isAuth: false,
    error: null,

}

const Auth_reducer = (state = intialState, action) => {

    switch (action.type) {
        case SET_USER_DATA: {
            return {
                ...state,
                ...state.payload,
                isAuth: true,


            }
        }
        case LOGIN_ERROR: {
            return {
                ...state, error: action.message
            }
        }

        default:
            return state
    }
}

export default Auth_reducer;

export const SetUserData = (Id, email, login, isAuth) => {
    return {
        type: SET_USER_DATA,
        payload: {
            Id, email, login, isAuth
        }
    }
}

export const loginError = (message) => {

    return {
        type: LOGIN_ERROR,
        message
    }
}


export const Loginthunk = () => async (dispatch) => {
    let response = await usersApi.loginAxios()
    if (response.data.resultCode === 0) {
        let {Id, email, login,} = response.data.data;
        dispatch(SetUserData(Id, email, login, true))
    }


}

export const Loginingthunk = (email, password, rememberMe) => async (dispatch) => {
    let response = await LoginApi.LoginAxios(email, password, rememberMe)
    if (response.data.resultCode === 0) {
        dispatch(Loginthunk())

    }

}


export const Logoutthunk = () => async (dispatch) => {
    let response = await LoginApi.LogoutAxios()

    if (response.data.resultCode === 0) {
        dispatch(SetUserData(null, null, null, false))

    } else {
        let message = response.data.messages.length > 0 ? response.data.messages[0] : "Login error";
        dispatch(loginError(message))
    }


}






