import React from 'react'
import {connect} from "react-redux";
import {
    SetUsersProfile,
    SetUsersProfileThunk,
    GetStatusThunk,
    UpdateThunk,
} from "../../../Redux/ProfilePage-reducer";
import Profile from './Profile'
import withRouter from "react-router-dom/es/withRouter";
import {WithAuthRedirect} from "../../../Hoc/WithAuthRedirect";
import {compose} from "redux";

class ProfileContainer1 extends React.Component {

    componentDidMount() {
        debugger
        let userId = this.props.match.params.userId
        if (!userId) {
            userId = this.props.authorizedUserId
        }
        this.props.SetUsersProfileThunk(userId)
        this.props.GetStatusThunk(userId)
    }

    render() {

        return (
            <Profile {...this.props} profile={this.props.profile} status={this.props.status}
                     UpdateThunk={this.props.UpdateThunk}/>
        )

    }
}

let mapSatetoProps = (state) => {
    return {
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        authorizedUserId: state.AuthReducer.userId,
        isAuth: state.AuthReducer.isAuth,

    }
}

export default compose(
    connect(mapSatetoProps, {SetUsersProfile, SetUsersProfileThunk, GetStatusThunk, UpdateThunk}),
    withRouter,
    WithAuthRedirect
)(ProfileContainer1)


